body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
/*images with the same height*/


.featurette-heading {
  font-size: 40px;
}

.text-muted {
  color: #6c757d!important;
}

.marketing .col-lg-4 {
  margin-bottom: 1.5rem;
  text-align: center;
}

.marketing h2 {
  font-weight: 400;
}

.marketing .col-lg-4 p {
  margin-right: .75rem;
  margin-left: .75rem;
}

.featurette-divider {
  margin: 5rem 0; /* Space out the Bootstrap <hr> more */
}

/* Thin out the marketing headings */
.featurette-heading {
  font-weight: 300;
  line-height: 1;
  letter-spacing: -.05rem;
}

.featurette-img-align {
  display: flex;
  align-items: center;
}

@media (min-width: 62em) {
  .featurette-heading {
    margin-bottom: 3rem;
  }
}

body {
  padding-bottom: 3rem;
}

.mt-8 {
  margin-top: 8rem !important;
}

.btn-social {
    color: white;
    padding: 20px;
    border-radius: 50%;
  
}
  
.fa-linkedin {
    background-color: #007bb5;
}
  
.fa-envelope {
    background-color: #263238;
}

.img-responsive {
    display: block;
    max-width: 100%;
    height: auto;
}

.fill-screen {
    height: 100vh;
}

.chewy-font {
    font-family: chewy;
}
.nav-link{
    font-family: chewy;
    font-size: 1.2rem;
  }

.nav-fixed-top {
    position: fixed;
    right: 0;
    left: 0;
    z-index: 1030;
    top: 0;
 }
  
.navbar-dark .navbar-toggler {
   border-color: transparent !important;
}

.navbar-brand {
  padding-top: 0rem!important;
 }

.bg-darkPurple{
  background-color: #4A148C!important
}
.btn-clear {
    background-color: transparent;
    border-color: transparent;
  }
  
  .btn-clear:hover {
    background-color: grey;
    border-color: grey;
  }
